                    </v-card-text>
                    <v-card-text class="text-xs-left py-2 pl-3 subheading">
                      {{ dato.nombre }}. {{ dato.parentezco }}
                    </v-card-text>
                    <v-card-text class="text-xs-left py-1 pl-3 subheading"
                      >{{ dato.cargo }} de {{ dato.empresa }}
                    </v-card-text>
                    <v-card-text class="text-xs-left py-1 pl-3 subheading"
                      >{{ dato.telefono }}
                    </v-card-text>
                  </v-card-text>
                </v-flex>
              </v-layout>
            </v-card-text>
          </TimeCard>
        </v-flex>
